import request from "@/utils/request";

export function updateUser(data) {
  let res = {
    code: 4033,
    message: "success"
  };
  return new Promise(resolve => {
    resolve(res)
  })

  return request({
    url: '/user/update',
    method: 'post',
    data: data
  })
}



